	module.exports = {"data":{"markdownRemark":{"html":"<p>Lisons <strong>Genèse 9:1,7,11 :</strong></p>\n<blockquote>\n<p>« Dieu bénit Noé et ses fils, et leur dit : Soyez féconds, multipliez, et remplissez la terre. Et vous, soyez féconds et multipliez, répandez-vous sur la terre et multipliez sur elle. J'établis mon alliance avec vous: aucune chair ne sera plus exterminée par les eaux du déluge, et il n'y aura plus de déluge pour détruire la terre. »</p>\n</blockquote>\n<p>Après le déluge, Dieu bénit Noé et sa famille afin qu'ils soient féconds, se multiplient et remplissent de nouveau la Terre.</p>\n<p>Néanmoins, quelques générations plus tard, les hommes choisirent de se rebeller contre Dieu en se regroupant en un seul lieu, au lieu de se répandre sur la Terre et en se lançant dans une construction prétentieuse, et même blasphématoire selon certains théologiens. C'est alors que Dieu confondit leur langage :</p>\n<blockquote>\n<p>« Toute la terre avait une seule langue et les mêmes mots. Comme ils étaient partis de l'orient, ils trouvèrent une plaine au pays de Schinear, et ils y habitèrent. Ils se dirent l'un à l'autre : Allons ! faisons des briques, et cuisons-les au feu. Et la brique leur servit de pierre, et le bitume leur servit de ciment. Ils dirent encore: Allons ! bâtissons-nous une ville et une tour dont le sommet touche au ciel, et faisons-nous un nom, afin que nous ne soyons pas dispersés sur la face de toute la terre. L'Eternel descendit pour voir la ville et la tour que bâtissaient les fils des hommes. Et l'Eternel dit : Voici, ils forment un seul peuple et ont tous une même langue, et c'est là ce qu'ils ont entrepris; maintenant rien ne les empêcherait de faire tout ce qu'ils auraient projeté. Allons ! descendons, et là confondons leur langage, afin qu'ils n'entendent plus la langue, les uns des autres. Et l'Eternel les dispersa loin de là sur la face de toute la terre; et ils cessèrent de bâtir la ville. C'est pourquoi on l'appela du nom de Babel, car c'est là que l'Eternel confondit le langage de toute la terre, et c'est de là que l'Eternel les dispersa sur la face de toute la terre. »\n<br><small>- Genèse 11:1-9</small></p>\n</blockquote>\n<p>La mission donnée par Jésus à ses disciples était de porter l'évangile au monde entier :</p>\n<blockquote>\n<p>« Allez, faites <strong>de toutes les nations</strong> des disciples, les baptisant au nom du Père, du Fils et du Saint-Esprit » <br><small>- Matthieu 28:19</small></p>\n</blockquote>\n<p>Mais cette tâche était clairement impossible à réaliser à taille humaine ! C'est la raison pour laquelle le Saint-Esprit leur fut envoyé par Jésus. Il devait leur accorder la force et la sagesse dont ils auraient besoin dans leur mission, mais aussi les équiper de dons divers pour l'édification de l'Église et pour faire face aux différents problèmes qui feraient obstacle à la propagation de l'évangile. Ces obstacles incluaient la barrière des langues, qui n'en était pas des moindres !</p>\n<blockquote>\n<p>« Mais vous recevrez une puissance, le Saint-Esprit survenant sur vous, et vous serez mes témoins à Jérusalem, dans toute la Judée, dans la Samarie, et jusqu'aux extrémités de la terre. » <br><small>- Actes 1:8</small></p>\n</blockquote>\n<blockquote>\n<p>« Puis il leur dit : Allez par tout le monde, et prêchez la bonne nouvelle à toute la création. Voici les miracles qui accompagneront ceux qui auront cru : en mon nom, ils chasseront les démons; <strong>ils parleront de nouvelles langues;</strong> » <br><small>- Marc 16:15,17</small></p>\n</blockquote>\n<p>Paul déclare d'ailleurs dans <strong>1 Corinthiens 14:22</strong> que le don des langues est <strong>surtout accordé pour atteindre les non-croyants :</strong></p>\n<blockquote>\n<p>« Par conséquent, les langues sont un signe, non pour les croyants, mais pour les non-croyants; la prophétie, au contraire, est un signe, non pour les non-croyants, mais pour les croyants. »</p>\n</blockquote>\n<p>Ce fut d'ailleurs le cas dans <strong>Actes 2</strong>, comme nous l'avons lu précédemment :</p>\n<blockquote>\n<p>« Ils étaient tous dans l'étonnement et la surprise, et ils se disaient les uns aux autres: Voici, ces gens qui parlent ne sont-ils pas tous Galiléens? Et comment les entendons-nous dans notre propre langue à chacun, dans notre langue maternelle? » <br><small>- Actes 2:7,8</small></p>\n</blockquote>","frontmatter":{"title":"D'où vient le don de parler en langues et quelle est son utilité ?"}}},"pathContext":{"slug":"/d-ou-vient-le-don-du-parler-en-langues-et-quel-est-son-utilite/"}}

